import Vue from 'vue'
import Vuex from 'vuex'
import {getRequest} from "../utils/api";
import SockJS from 'sockjs-client';
import Stomp from 'stompjs';
import current from "element-ui/packages/table/src/store/current";

Vue.use(Vuex)
const now = new Date();

const store = new Vuex.Store({
    state: {
        routes: [],
        hrs: [],
        stomp: null, //websocket连接对象
        currentHr: null,
        depJobDemo:{},
        sessions: {},
        // sessions: [
        //     {
        //     id:1,
        //     user:{
        //         name:'示例介绍',
        //         img:'https://img.tupianzj.com/uploads/allimg/200224/37-200224111Q40-L.jpg'
        //     },
        //     messages:[{
        //         content:'Hello，这是一个基于Vue + Vuex + Webpack构建的简单chat示例，聊天记录保存在localStorge, 有什么问题可以通过Github Issue问我。',
        //         date:now
        //     },{
        //         content:'项目地址(原作者): https://github.com/coffcer/vue-chat',
        //         date:now
        //     },{
        //         content:'本项目地址(重构): https://github.com/is-liyiwei',
        //         date:now
        //     }]
        // },{
        //     id:2,
        //     user:{
        //         name:'桃红',
        //         img:'http://img3.imgtn.bdimg.com/it/u=2505845389,3637097424&fm=26&gp=0.jpg'
        //     },
        //     messages:[{
        //         content:'Hi，我是小桃红哦',
        //         date:now
        //     }]
        // }
        // ],
        currentSession: -1,
        filterKey: ''
    },
    mutations: {  //调用此处方法要使用  eg. store.commit.('initRoutes')
        init_currentHr(state, hr) {
            state.currentHr = hr;
        },
        initRoutes(state, data) {
            state.routes = data
        },
        changecurrentSession(state, currentSession) {
            state.currentSession = currentSession;
        },
        addMessage(state, msg) {
            let Msg = state.sessions[state.currentHr.username + '#' + msg.to];
            if (!Msg) {
                // state.sessions[state.currentHr.username+'#'+msg.to]=[]; 不能实时更新数据。
                Vue.set(state.sessions, state.currentHr.username + '#' + msg.to, []);
            }
            state.sessions[state.currentHr.username + '#' + msg.to].push({
                content: msg.content,
                date: new Date(),
                self: !msg.notSelf
            })
        },
        INIT_DATA(state) {
            //浏览器本地历史聊天记录
            let data = localStorage.getItem('vue-chat-session');
            console.log(data)
            if (data) {
                state.sessions = JSON.parse(data);
            }
        },
        INIT_HR(state, data) {
            state.hrs = data;
        },
        INIT_DEP(state, data) {
            let msg = state.depJobDemo[state.currentHr.username];
            if (!msg){
                Vue.set(state.depJobDemo, state.currentHr.username, [])
            }
            state.depJobDemo[state.currentHr.username]=data;
        }
    },
    actions: {                              // 异步，主动触发mutation的方法 调用此处方法要使用 eg. store.dispatch('connect');
        connect(context) {
            context.state.stomp = Stomp.over(new SockJS('/ws/ep')); //连接对象
            context.state.stomp.connect({}, success => {   //发起连接

                context.state.stomp.subscribe('/user/queue/chat', msg => { //默认会加上 /user
                    let receiveMsg = JSON.parse(msg.body);
                    receiveMsg.notSelf = true;
                    receiveMsg.to = receiveMsg.from;
                    context.commit('addMessage', receiveMsg);
                });
                context.state.stomp.subscribe('/user/queue/job', msg => {
                    console.log(msg)
                    context.commit('INIT_DEP', JSON.parse(msg.body))
                })

            }, error => {

            })
        },
        // quartzJob(context) {
        //     context.state.stompJob = Stomp.over(new SockJS('/ws/quartz'));
        //     context.state.stompJob.connect({}, success => {
        //             context.state.stompJob.subscribe('/user/queue/job', msg => {
        //                 context.commit('INIT_DEP', JSON.parse(msg.body))
        //                 console.log(msg.body)
        //             })
        //         }, error => {
        //
        //         }
        //     )
        // },
        initData(context) {
            context.commit('INIT_DATA');
            getRequest("/chat/hrs").then(resp => {
                if (resp) {
                    context.commit('INIT_HR', resp)
                }
            })
        }
    }
})
store.watch(function (state) {
    return state.sessions
}, function (val) {
    console.log('CHANGE: ', val);
    localStorage.setItem('vue-chat-session', JSON.stringify(val));
}, {
    deep: true/*这个貌似是开启watch监测的判断,官方说明也比较模糊*/
})


export default store;