<template>
    <div>
        <el-container>
            <el-header class="homeHeader">
                <div class="title">微人事</div>
                <div>
                    <el-button icon="el-icon-bell" type="text" style="margin-right: 8px;color:#000000 " size="large" @click="goChat"></el-button>
                    <el-dropdown class="dropDownUserInfo" @command="commandHandler">
  <span class="el-dropdown-link">
      {{user.name}}<i><img :src="user.userface" alt=""></i>
  </span>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item command="userinfo">个人中心</el-dropdown-item>
                            <el-dropdown-item command="setting">设置</el-dropdown-item>
                            <el-dropdown-item command="logout" divided>注销登录</el-dropdown-item> <!-- 有分割线-->
                        </el-dropdown-menu>
                    </el-dropdown>
                </div>
            </el-header>
            <el-container>
                <el-aside width="200px">
                    <el-menu router unique-opened> <!--左侧导航栏-->
                        <el-submenu :index="index+''" v-for="(item,index) in route" v-if="!item.hidden" :key="index">
                            <template slot="title">
                                <i style="color: #5affcd;margin-right: 5px" :class="item.iconCls"></i>
                                <span>{{item.name}}</span>
                            </template>
                            <el-menu-item v-for="(itemi,indexi) in item.children" :index="itemi.path" :key="indexi">
                                {{itemi.name}}
                            </el-menu-item>
                        </el-submenu>
                    </el-menu>
                </el-aside>
                <el-main>
                    <el-breadcrumb separator-class="el-icon-arrow-right" v-if="this.$router.currentRoute.path!='/home'">
                        <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
                        <el-breadcrumb-item>{{this.$router.currentRoute.name}}</el-breadcrumb-item>
                        <!--                        导航-->
                    </el-breadcrumb>
                    <div class="homeWelcome" v-if="this.$router.currentRoute.path=='/home'">欢迎来到微人事！</div>
                    <router-view class="homeRouterView"/> <!--内容展示区-->
                </el-main>
            </el-container>
        </el-container>
    </div>
</template>

<script>
    export default {
        name: "Home",
        data() {
            return {
                user: JSON.parse(window.sessionStorage.getItem("user"))
            }
        },
        computed: {
            route() {
                return this.$store.state.routes;
            }
        },
        methods: {
            goChat(){
                this.$router.push("/chat");
            },
            commandHandler(cmd) { //只有dropdown才可以使用
                if (cmd == 'logout') {
                    this.$confirm('此操作将注销登录, 是否继续?', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        this.getRequest('/logout');
                        window.sessionStorage.removeItem("user")
                        this.$router.replace("/")
                        this.$store.commit('initRoutes', [])
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: '已取消操作'
                        });
                    });
                }
            },
        }
    }
</script>

<style>
    .homeHeader {
        background-color: #77a6ff;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0px 15px;
        box-sizing: border-box;
    }

    .homeHeader .title {
        font-size: 30px;
        font-family: 华文行楷;
        color: white;
    }

    .homeHeader .dropDownUserInfo {
        cursor: pointer;
    }

    .el-dropdown-link img {
        width: 48px;
        height: 48px;
        border-radius: 24px;
        margin-left: 8px;
    }

    .el-dropdown-link {
        display: flex;
        align-items: center;
    }

    .homeWelcome {
        text-align: center;
        font-size: 30px;
        font-family: STXingkai;
        color: #77a6ff;
        padding-top: 50px;
    }

    .homeRouterView {
        margin-top: 10px;
    }
</style>