<template>
    <div>
        <el-form :rules="rules" :model="loginForm" class="loginContainer" ref="formLogin"
                 v-loading="loading"
                 element-loading-text="正在登陆..."
                 element-loading-spinner="el-icon-loading"
                 element-loading-background="rgba(0, 0, 0, 0.8)">
            <h3 class="loginTitle">系统登录</h3>
            <el-form-item prop="username">
                <el-input type="text" v-model="loginForm.username" auto-complete="off" placeholder="请输入用户名"></el-input>
            </el-form-item>
            <el-form-item prop="password">
                <el-input type="text" v-model="loginForm.password" auto-complete="off" placeholder="请输入密码" show-password @keydown.enter.native="submitLogin"></el-input>
            </el-form-item>
            <el-checkbox v-model="checked" class="loginRadio"></el-checkbox>
            <el-button type="primary" style="width: 100%;" @click="submitLogin">登录</el-button>
        </el-form>
    </div>
</template>

<script>
    export default {
        name: "Login",
        data() {
            return {
                loginForm: {//输入框默认值
                    username: 'admin',
                    password: '123'
                },
                checked: true,
                loading:false,
                rules: { //错误提醒
                    username: [{required: true, message: '请输入用户名', trigger: 'blur'}],
                    password: [{required: true, message: '请输入密码', trigger: 'blur'}]
                }
            }
        },
        methods: {
            submitLogin() {
                this.$refs.formLogin.validate((valid) => {
                    if (valid) {
                        this.loading=true
                        // this.$message.success('校验成功！')
                        this.postKeyValueRequest('/doLogin', this.loginForm).then(resp => {
                            this.loading=false
                            if (resp) {
                                window.sessionStorage.setItem("user", JSON.stringify(resp.obj));
                                this.$store.commit('init_currentHr',resp.obj);
                                let path= this.$route.query.redirect;
                                this.$router.replace((path=='/' || path==undefined)?'/home':path);
                            }
                        })
                    } else {
                        this.$message.error('请完整输入表单');
                        return false;
                    }
                })
            }
        }
    }
</script>

<style>
    .loginContainer {
        border-radius: 15px; /*边框*/
        background-clip: padding-box; /*卡片效果*/
        margin: 180px auto; /*外边距*/
        width: 350px;
        padding: 15px 35px 25px 35px; /*内边距 上右下左*/
        background: #fff;
        border: 1px solid #eaeaea; /*边框线*/
        box-shadow: 0 0 25px #cac6c6; /*阴影效果  数据意义：左右偏移，上下偏移，整体大小*/
    }

    .loginTitle {
        margin: 15px auto 20px auto;
        text-align: center;
        color: #505458;
    }

    .loginRadio {
        margin: 0px 0px 10px 0px;
    }
</style>