import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../views/Login.vue'
import Home from '../views/Home.vue'
import FriendChat from "../views/chat/FriendChat";

Vue.use(VueRouter)
const routes = [
    {
    path: '/',
    name: 'Login',
    component: Login , //加载的组件名
    hidden:true
  },{
        path: '/home',
        name: 'Home',
        component: Home,
        hidden:true,
        children:[
            {
                path: '/chat',
                name: 'FriendChat',
                component: FriendChat , //加载的组件名
                hidden:true
            }
        ]
    },
]

const router = new VueRouter({
  routes
})

export default router
