import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import {postKeyValueRequest} from './utils/api.js'
import {postRequest} from './utils/api.js'
import {putRequest} from './utils/api.js'
import {getRequest} from './utils/api.js'
import {deleteRequest} from './utils/api.js'
import fr from "element-ui/src/locale/lang/fr";
import {initMenu} from "./utils/menus";
import 'font-awesome/css/font-awesome.min.css'

Vue.prototype.postKeyValueRequest=postKeyValueRequest;
Vue.prototype.postRequest=postRequest;
Vue.prototype.putRequest=putRequest;
Vue.prototype.getRequest=getRequest;
Vue.prototype.deleteRequest=deleteRequest;

Vue.use(ElementUI)
Vue.config.productionTip = false
router.beforeEach((to, from, next) => {
  if (to.path=='/'){
    next();
  }else {
    if(window.sessionStorage.getItem('user')){
      initMenu(router,store);
      next();
    }else {
      next('/?redirect='+to.path);  //redirect 记载了登录成功之后跳转的界面
    }
  }
})

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')//渲染App.vue
